import React, { Component } from 'react';
import $ from 'jquery';
import Card from './card';

const config = require('./../config');

export default class Carousel extends Component {
    constructor(props){
        super(props);
        this.keyword = this.props.keyword;
        this.scrollDuration = 600;
        this.length = 0;
        this.menuPosition = 0;
        this.paddleMargin = 20;
        this.list = React.createRef();
        this.leftclick = React.createRef();
        this.rightclick = React.createRef();
        this.menuName = "#"+this.keyword+"-menu";
        this.position = 0;
        this.content = [];
        this.count = 0;

        this.updateSize = this.updateSize.bind(this);
        this.getMenuWrapperSize = this.getMenuWrapperSize.bind(this);
        this.getMenuSize = this.getMenuSize.bind(this);
        this.getMenuInvisibleSize = this.getMenuInvisibleSize.bind(this);
        this.getMenuPosition = this.getMenuPosition.bind(this);
        this.scrollLeft = this.scrollLeft.bind(this);
        this.scrollRight = this.scrollRight.bind(this);
    }

    componentDidMount(){
        this.resize(this);
        window.addEventListener("resize", this.resize(this));
    }

    resize(component){
        component.menuPosition = component.position * component.getMenuWrapperSize();
        $(component.menuName).animate({ scrollLeft: component.menuPosition}, 0);
        component.hidePaddles(component.scroll(component.getMenuPosition(), component.getMenuWrapperSize(), component.getMenuSize(component.length, component.updateSize()), component.getMenuInvisibleSize()));
    }

    updateLength(x){
        this.length = x;
    }

    updateSize(){
        return $('.'+this.keyword+'item').outerWidth(true);
    }

    getMenuWrapperSize(){
        return $(this.menuName).outerWidth(true);
    }

    getMenuSize(length, width){
        return length * width;
    }

    getMenuInvisibleSize(){
        return this.getMenuSize(this.length, this.updateSize()) - this.getMenuWrapperSize();
    }

    getMenuPosition(){
        return $(this.menuName).scrollLeft();
    }

    getScrollAmount(menuPos, wrapperSize, total, dir){
        let width = this.getMenuWrapperSize()+1;
        if(typeof(width) != Number)
        {
            width = wrapperSize
        }
        if(dir === "left"){
            this.pos = (menuPos-width) < 0 ? 0 : this.pos--;
            return (menuPos - width) < 0 ? 0 : menuPos - width;
        }
        else if (dir === "right"){
            this.pos = (menuPos+width) > total ? count : this.pos++;
            return (menuPos + width) > total ? total : menuPos + width;
        }
        else{
            new Error(['invalid direction passed when determining scroll amount', 'carousel.js', '71']);
        }
    }

    scrollLeft(){
        var scrollAmount = this.getScrollAmount($(this.menuName).scrollLeft(), this.getMenuWrapperSize(), this.getMenuSize(this.length, this.updateSize()), "left");
        var scrollDuration = 300;
        this.menuPosition = scrollAmount;
        $(this.menuName).animate({ scrollLeft: scrollAmount}, scrollDuration);
        var paddleVisibility = this.scroll(this.menuPosition, this.getMenuWrapperSize(), this.getMenuSize(this.length, this.updateSize()), this.getMenuInvisibleSize());
        this.hidePaddles(paddleVisibility);    }

    scrollRight(){
        var scrollAmount = this.getScrollAmount($(this.menuName).scrollLeft(), this.getMenuWrapperSize(), this.getMenuSize(this.length, this.updateSize()), "right");
        var scrollDuration = 300;
        this.menuPosition = scrollAmount;
        $(this.menuName).animate({ scrollLeft: scrollAmount}, scrollDuration);
        this.hidePaddles(this.scroll(this.menuPosition, this.getMenuWrapperSize(), this.getMenuSize(this.length, this.updateSize()), this.getMenuInvisibleSize()));
    }

    scroll(pos, visibleWidth, totalWidth, invisibleSize){
        //Pos will be null when rendering component for tests, return insignificant value in this case
        if(pos == null){
            return [0,0];
        }

        if(invisibleSize <= 0){
            //hide both paddles
            return [0,0];
        }
        else if(pos <= 0 ) {
            //hide left
            return [0,1];
        }
        else if(pos + visibleWidth < totalWidth){
            //show both
            return [1, 1];
        }
        else if(pos + visibleWidth >= totalWidth){
            //hide right
            return [1, 0];
        }
    }

    hidePaddles(vis){
        var leftpaddle = "#"+this.keyword+"-left";
        var rightpaddle = "#"+this.keyword+"-right";

        if(vis[0]){
            $(leftpaddle).removeClass('hidden');
        }
        else{
            $(leftpaddle).addClass('hidden');
        }
        if(vis[1]){
            $(rightpaddle).removeClass('hidden');
        }
        else{
            $(rightpaddle).addClass('hidden');
        }
    }


    render(){
        var jsonString = config.getData();
        var json = JSON.parse(jsonString);
        var data = json[this.keyword];
        this.length = data.length;
            return (
            <div>
                <ul className={["menu", this.keyword].join(' ')} id={[this.keyword,"menu"].join('-')} ref={this.list}>
                    {data.map((obj, index) => {
                        this.count++;
                        return(<li className={[this.keyword+"item", "item"].join(' ')} key={index} id={this.keyword+"item-"+index}>
                                <Card data={obj} key={obj.link} topics={obj.topics}/> 
                                </li>)
                    })}
                </ul>
                <div class="paddles">
                    <button class="left-paddle paddle hidden noselect" id={[this.keyword,"left"].join('-')} onClick={this.scrollLeft} ref={this.leftclick}><i class="ms-Icon ms-Icon--ChevronLeftSmall" aria-hidden="true"></i></button>
                    <button class="right-paddle paddle noselect" id={[this.keyword,"right"].join('-')} onClick={this.scrollRight} ref={this.rightclick}><i class="ms-Icon ms-Icon--ChevronRightSmall" aria-hidden="true"></i></button>
                </div>
            </div>
        );
    }

}
