import React, { Component } from 'react';
import SearchWidget from './searchWidget';
import { DefaultButton } from 'office-ui-fabric-react/lib/Button';
import { SearchBox } from 'office-ui-fabric-react/lib/SearchBox';
import { ChoiceGroup, IChoiceGroupOption } from 'office-ui-fabric-react/lib/ChoiceGroup';
import $ from 'jquery';


export default class Search extends Component {
    constructor(props){
        super(props);
        this.state={
            inputValue: '',
            keyword: ''
        };
    }

    componentDidMount(){
        this.setState({
            inputValue: ''
        })
        this.setKeyword()
    }

    setKeyword(evt){
        if(evt === undefined){
            this.setState({
                keyword: '' 
            })
        }
        else{
            this.setState({
                keyword: evt
            })
        }
    }

    updateInput(evt){
        this.setState({
            inputValue: new String(evt)
        });
        this.setKeyword();
    }

    render(){
        return (
            <div>
                <div class="content-container">
                     <div class="ms-Grid-col ms-sm12 ms-md4 ms-lg3">
                
                        <ChoiceGroup
                            className="defaultChoiceGroup"
                            defaultSelectedKey="B"
                            options={[
                            {
                                key: 'Models',
                                text: 'Models',
                                'data-automation-id': 'auto1'
                            },
                            {
                                key: 'Experiments',
                                text: 'Experiments',
                                disabled:true
                            },
                            {
                                key: 'Tutorials',
                                text: 'Tutorials',
                                disabled: true
                            }
                            ]}
                            onChange={this._onChange}
                            label="Filter By:"
                            required={true}
                        />
                    </div>
                    <div class="ms-Grid-col ms-sm12 ms-md8 ms-lg9">

                        <SearchBox
                            placeholder="Search"
                            id='keyword'
                            name='keyword'
                            onSearch={newValue => this.setKeyword(newValue)&console.log(newValue)}
                            onFocus={() => console.log('onFocus called')}
                            onBlur={() => console.log('onBlur called')}
                            onChange={() => console.log('onChange called')}
                        />
                        <SearchWidget keyword={this.state.keyword}/>

                    </div>
                </div>
            </div>
        )
    }
}
