import React, { Component } from 'react';
import GithubApiInterface from './../githubApiInterface';

export default class Card extends Component {
    constructor(props){
        super(props);
        this.data = this.props.data;
        this.topics = this.props.topics;
        this.state={
            content: "",
            link: "",
            userlink: "",
            imageLink: "",
            userName: "",
            repoName: "",
            numForks: "",
            numStars: "",
            repoLink: "",
            topics: this.props.topics ? this.props.topics : null
        };
    }

    componentDidMount(){
        this.parseData(this.data);
    }

    parseData(response){
        this.setState({
            imageLink: response.owner.avatar_url,
            imageLink: response.owner.avatar_url,
            userlink: response.owner.html_url,
            userName: response.owner.login,
            repoName: response.name,
            numForks: response.forks_count,
            numStars: response.stargazers_count,
            repoLink: response.html_url,
            content: response.description,
            link:["https://www.github.com", response.owner.login, response.name,].join('/'),
            topics: response.topics
        })
    }

    returnTopics(topics){
        var topics = [];
        for (var i=0; i < this.state.topics.length && i < 6; i++){
            topics.push(<div className={["repo-topic","repo-attr"].join(' ')}>{this.state.topics[i]}</div>);
        };
        return(
            topics.map(function(topic){
                return <li>{topic}</li>;
            })
        )
    }

    render(){
            return (
            <div>
                <div class="ms-Grid-col ms-sm12 ms-md12 ms-lg12 featured-container">
                    <div class="ms-Grid-row">
                        <div class="ms-Grid-col ms-sm1 ms-md1  ms-xl1"></div>
                        <div class="ms-Grid-col hiddenLgDown ms-lg12 ms-xl4">
                                <a className={"avatar hiddenLgDown"} href={this.state.userlink} target={"_top"}>
                                    <img src={this.state.imageLink} alt={this.state.userName} width={200} height={200}/>
                                </a>
                        </div>
                        <div class="ms-Grid-col ms-sm10 ms-md10 ms-lg12 ms-xl6">
                            <div className={["github-card","repo-card"].join(' ')}>
                                <div className={["header"]}>
                                    <div class="ms-Grid-row">
                                        <h1><a className={"wordwrap heading"} href={this.state.link} target={"_top"}>{this.state.repoName}</a></h1>
                                    </div>
                                </div>
                                <ul className={"status"}>
                                    <li>
                                        <a href={[this.state.link, "/network"].join('')} className={"repo-status"} target="_top">
                                            <strong>{this.state.numForks} </strong>
                                            Forks
                                        </a>
                                    </li>
                                    <li>
                                        <a href={[this.state.link, "/stargazers"].join('')} className={"repo-status"} target="_top">
                                            <strong>{this.state.numStars} </strong>
                                            Stars
                                        </a>
                                    </li>
                                </ul>
                                <div className={"content wordwrap"}>
                                    <p>{this.state.content}</p>
                                </div>
                                <ul className={"topics"}>
                                    {this.returnTopics(this.state.topics)}
                                </ul>
                            </div>
                        </div>
                        <div class="ms-Grid-col hiddenLgDown ms-xl1"></div>
                    </div>
                </div>
            </div>
        )
    }

}
